<!-- 读堑 -->
<template lang="html">
    <transition name="fan">
        <div id="read-qian">
            <div class="cover">
                <img :src="qian.mainImg" alt="封面">
            </div>
            <div class="content">
                <div class="title">
                    <h1>{{qian.title}}</h1>
                </div>
                <div class="date-tags">
                    <span class="date">{{dtUtils.toFormatString(new Date(qian.createDate))}}</span>
                    <Tag :value="qian.tags" :readonly="true"></Tag>
                </div>
                <div class="article">
                    <div class="ql-container ql-snow">
                        <article class="ql-editor"  v-html="qian.article"></article>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import Tag from 'cpn/tag/Tag';

import dtUtils from 'utils/datetime';

import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';

export default {
    components: {
        Tag
    },
    mounted() {
        this.init();
    },
    data() {
        return {
            dtUtils,
            qian: {}
        };
    },
    methods: {
        init() {
            this.qian = this.$route.params.qian;
            console.log(this.qian);
        }
    }
};
</script>

<style lang="less">
#read-qian {
    width: 100%;
    height: 100%;
    overflow: auto;

    & > div {
        width: 100%;
    }

    .cover {
        height: 50vw;
        overflow: visible;
        position: relative;
        img {
            width: 100%;
            height: auto;
            position: absolute;
            bottom: 0;
        }
    }
}
</style>
